@forward "sass:math";

@mixin calc-fontsize($size, $displaysize) {
  font-size: math.div($size, $displaysize) * 100vw;
}

@mixin l_height($pixels, $height, $displaysize) {
  font-size: math.div($pixels, $displaysize) * 100vw;
  line-height: math.div($height, $pixels);
}

@mixin smp {
  @media (max-width: ($bp)) {
      @content;
  }
}
@mixin tab {
  @media (min-width: ($bp + 1)) {
      @content;
  }
}
@mixin pc {
  @media (min-width: ($bp-pc + 1)) {
      @content;
  }
}
@mixin lg {
  @media (min-width: ($bp-lg + 1)) {
      @content;
  }
}

$sp-width: 767px;
$pc-width: 1600px;

// サイズ
$bp: 800px;
$bp-pc: 1024px;
$bp-lg: 1600px;
$sp-width: 750px;
$sp-inner: 660px;
$tab-width: 800px;
