@charset "UTF-8";
@use "./base" as base;
@use "sass:math";

@mixin calc-fontsize($size, $displaysize) {
  font-size: math.div($size, $displaysize) * 100vw;
}

@mixin calc-height($pixels, $height, $displaysize) {
  font-size: math.div($pixels, $displaysize) * 100vw;
  line-height: math.div($height, $pixels);
}

@mixin l_height($pixels, $height) {
  font-size: $pixels * 1px;
  line-height: math.div($height, $pixels);
}

$green: #429137;
$blue: #29abe2;

body,
button {
  margin: 0 auto;
  font-family: fot-tsukuardgothic-std, "游ゴシック Medium", "Yu Gothic Medium",
    "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", sans-serif;
  font-weight: 400;
  font-style: normal;
  // overflow-x: hidden;
}

img {
  width: 100%;
  vertical-align: bottom;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
dl,
dt,
dd {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #000;
}

.hidden-phone {
  display: none !important;
}

@include base.tab {
  .visible-phone {
    display: none !important;
  }

  .hidden-phone {
    display: inherit !important;
    position: relative;
  }
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.float-l {
  float: left;
}

.float-r {
  float: right;
}

.mizuki_fv {
  &-bg {
    width: 100%;
    height: 620px;
    position: relative;
    background-image: url(../img-mizuki/fv-sp.png);
    background-size: cover;
    background-position: 70%;
    background-repeat: no-repeat;

    @include base.tab {
      // height: 500px;
      background-image: url(../img-mizuki/fv-pc.png);
      background-size: cover;
    }
  }

  h1 {
    position: absolute;
    top: 5%;
    left: 10%;
    @include l_height(25, 51);
    text-shadow: 2px 2px 0 #fff, -2px -2px 0 #fff, -2px 2px 0 #fff,
      2px -2px 0 #fff, 0px 2px 0 #fff, 0-2px 0 #fff, -2px 0 0 #fff, 2px 0 0 #fff;

    @include base.tab {
      width: 450px;
      top: 20%;
      left: 50%;
      transform: translateX(-50%);

      span {
        margin-left: 15%;
      }
    }
  }

  p {
    position: absolute;
    bottom: 20%;
    right: 5%;
    @include l_height(14, 26);
    font-weight: bold;

    @include base.tab {
      width: max-content;
      left: 50%;
      transform: translateX(-50%);
      @include l_height(20, 41);
    }
  }

  .mizuki_about_dna-test {
    width: 100%;
    position: absolute;
    bottom: -5%;
    left: 0;
    text-align: center;

    @include base.tab {
      bottom: -40px;
    }
  }

  &-text_outer {
    width: 100%;
    height: 100px;
    background-image: url(../img-mizuki/text-outer+deco.png);
    background-size: 360px;
    background-repeat: no-repeat;
    background-position: 50%;

    @include base.tab {
      background-size: 450px;
    }

    p {
      width: max-content;
      position: absolute;
      top: 39%;
      left: 51%;
      text-align: center;
      transform: translate(-50%, -50%);
      @include l_height(13, 23);

      @include base.tab {
        top: 6%;
        left: 51%;
        @include l_height(20, 36);
      }
    }

    span {
      padding-right: 0.5rem;
      @include l_height(18, 32);

      @include base.tab {
        @include l_height(24, 32);
      }
    }
  }

  a {
    display: inline-block;
    position: absolute;
    bottom: -25%;
    left: 50%;
    transform: translateX(-50%);
    @include l_height(12, 20);
    border-bottom: 1px solid #000;

    @include base.tab {
      @include l_height(14, 22);
    }
  }
}

.mizuki_search {
  padding-top: 8rem;

  &-dogs,
  &-cats {
    margin: 0 auto;
    width: 100%;
    max-width: 340px;

    @include base.tab {
      max-width: 900px;
    }

    &_title {
      position: relative;
      &-decoration {
        position: absolute;
        top: 10%;
        left: 5%;

        @include base.tab {
          left: 7%;
        }
      }
    }

    &-button {
      margin: 0 auto;
      border-radius: 24px;

      @include base.tab {
        margin: 0 auto;
        width: 90%;
        max-width: 900px;
      }

      &_list {
        padding: 2rem 0;

        @include base.tab {
          padding: 0;
          display: flex;
          align-items: center;
        }

        &-item {
          margin: 0 auto;
          width: 90%;
          background-color: #fff;
          border-radius: 13px;

          @include base.tab {
            width: 30%;
            height: fit-content;
          }

          &:nth-child(2) {
            margin: 1rem auto;
          }

          a {
            padding-left: 18%;
            width: -webkit-fill-available;
            display: inline-block;
            background-color: transparent;
            border: none;
            text-align: left;
            @include l_height(18, 45);

            @include base.tab {
              padding-left: 10px;
            }

            &::before {
              margin-right: 10px;
              content: "";
              display: inline-block;
              width: 25px;
              height: 25px;
              background-image: url(../img-mizuki/magnifying-glass.png);
              background-size: contain;
              background-repeat: no-repeat;
              vertical-align: text-top;

              @include base.tab {
                margin-right: 0;
              }
            }
          }

          &:hover {
            transform: scale(1.05);
          }
        }
      }
    }
  }

  &-dogs {
    h3 {
      color: $green;
    }

    &_title {
      &-decoration {
        width: 63px;
        height: 63px;
      }
    }

    &-button {
      background-color: $green;
    }
  }

  &-cats {
    padding-top: 3rem;

    h3 {
      color: $blue;
    }

    &_title {
      &-decoration {
        width: 50px;
        height: 57px;
        top: 25%;
      }
    }

    &-button {
      background-color: $blue;
    }
  }

  h3 {
    margin-left: 15%;
    margin-bottom: 0.2rem;
    padding: 0.3rem 2rem;
    width: max-content;
    border: 1px solid #000;
    border-radius: 25px;

    @include base.tab {
      margin-left: 11%;
    }
  }
}

#mizuki_modal-bleeder_dogs.active,
#mizuki_modal-breed_dogs.active,
#mizuki_modal-area_dogs.active,
#mizuki_modal-bleeder_cats.active,
#mizuki_modal-breed_cats.active,
