.mizuki_modal {
  // display: none;
  ::-webkit-scrollbar {
    display: none;
  }

  &-bg {
    display: none;

    width: 100%;
    height: 100%;
    position: fixed;
    top: 0%;
    left: 0%;
    // transform: translate(-50%, -50%);
    background-color: rgba($color: #000000, $alpha: 0.5);
    z-index: 99;
  }

  &-outer {
    margin: 0 auto;
    width: 90%;
    height: 100vh; /* Fallback */
    height: calc(var(--vh, 1vh) * 80);
    padding-top: 1rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    border-radius: 24px;
    box-shadow: 0px 0px 4px rgba($color: #848484, $alpha: 0.75);
    overflow-y: scroll;

    @include base.tab {
      max-width: 1200px;
      height: 100vh; /* Fallback */
      height: calc(var(--vh, 1vh) * 80);
    }

    &-dogs {
      ::-webkit-scrollbar-track {
        background: #dbead9;
      }

      ::-webkit-scrollbar-thumb {
        background: $green;
      }

      ::-webkit-scrollbar {
        display: inherit;
        width: 10px;
      }
    }

    &-cats {
      ::-webkit-scrollbar-track {
        background: #d9e7ea;
      }

      ::-webkit-scrollbar-thumb {
        background: $blue;
      }

      ::-webkit-scrollbar {
        display: inherit;
        width: 10px;
      }
    }
  }

  &-container {
    margin: 0 auto;
    width: 95%;
    height: 90%;
    // overflow: hidden;
  }

  &-close {
    width: 40px;
    height: 40px;
    position: fixed;
    top: 10px;
    right: 15px;
    z-index: 999;
    background-color: transparent;
    border: none;

    @include base.tab {
      top: 7px;
      right: 30px;
    }

    span {
      position: absolute;
      display: inline-block;
      width: 30px;
      height: 4px;
      top: 45%;
      left: 15%;
      border-radius: 2px;
      background-color: #000;

      &:nth-child(1) {
        transform: rotate(45deg);
      }
      &:nth-child(2) {
        transform: rotate(135deg);
      }
    }
  }

  &_title {
    margin: 0 auto;
    position: relative;
    h3 {
      padding-left: 100px;
      @include l_height(18, 36);
    }

    &-dogs {
      h3 {
        padding-bottom: 0.3rem;
        color: $green;
        border-bottom: 5px solid $green;
      }
    }
    &-cats {
      h3 {
        padding-bottom: 0.3rem;
        color: $blue;
        border-bottom: 5px solid $blue;
      }
    }
    &-decoration {
      position: absolute;
      top: -15%;
      left: 3%;

      &-dogs {
        width: 63px;
        height: 63px;
      }
      &-cats {
        top: 3%;
        width: 50px;
        height: 57px;
      }
    }
  }

  h4 {
    margin-top: 1rem;
    margin-left: 3%;
    @include l_height(18, 40);

    @include base.tab {
      @include l_height(21, 45);
    }

    &::before {
      margin-right: 10px;
      content: "";
      display: inline-block;
      width: 30px;
      height: 30px;
      background-image: url(../img-mizuki/magnifying-glass.png);
      background-size: contain;
      background-repeat: no-repeat;
      vertical-align: text-top;
    }
  }

  h5 {
    @include l_height(14, 36);

    @include base.tab {
      @include l_height(15, 40);
    }
  }

  &-form {
    margin: 0 auto;
    @include l_height(16, 36);
    max-width: 900px;

    @include base.tab {
      height: 90%;
      max-height: 700px;
    }

    .flex {
      display: block;
      align-items: flex-start;

      @include base.tab {
        display: flex;
        height: 90%;
      }
    }

    &_size,
    &_breed,
    &_area {
      max-width: 500px;
      border: 1px solid #ccc;
      border-collapse: collapse;
      padding-bottom: 1rem;
      box-sizing: border-box;

      @include base.tab {
        max-width: 800px;
      }

      input[type="checkbox"] {
        display: none;
      }
      &-checkbox {
        display: inline-block;
        position: relative;
        margin-left: 10px;
        padding: 7px 0 7px 30px;
        cursor: pointer;
        overflow-wrap: break-word;
        word-break: initial;
        @include l_height(16, 26);

        @include base.tab {
          margin-left: 0;
        }

        &::before {
          width: 16px; /* チェックボックスの幅 */
          height: 16px; /* チェックボックスの高さ */
          left: 5px;
          border: 1px solid #ccc; /* チェックボックスの枠 */
          margin-top: -8px;
        }

        &::before,
        &::after {
          position: absolute;
          content: "";
          top: 50%;
        }

        &::after {
          position: absolute;
          width: 5px;
          height: 9px;
          top: 50%;
          left: 10px;
          border-right: 2px solid #000; /* チェックマークの色 */
          border-bottom: 3px solid #000; /* チェックマークの色 */
          content: "";
          margin-top: -7px;
          opacity: 0;
          transform: rotate(45deg);
        }
      }

      input[type="checkbox"]:checked + .mizuki_modal-form_size-checkbox::after,
      input[type="checkbox"]:checked + .mizuki_modal-form_breed-checkbox::after,
      input[type="checkbox"]:checked + .mizuki_modal-form_area-checkbox::after {
        opacity: 1;
      }
    }

    &_size {
      &-dogs {
        margin: 0 auto;
        width: 85%;
        height: 300px;

        @include base.tab {
          margin-right: 0;
          height: 90%;
          width: 30%;
        }
      }
    }

    &_breed {
      &-dogs,
      &-cats {
        margin: 0 auto;
        margin-top: -1px;
        width: 85%;
        height: 300px;

        overflow-y: scroll;

        @include base.tab {
          margin-top: 0;
          margin-left: -1px;
          width: 100%;
          height: 90%;
        }
      }
    }

    &_area {
      margin: 0 auto;
      width: 85%;
      height: 300px;

      overflow-y: scroll;

      @include base.tab {
        width: 100%;
        height: 90%;
        flex-wrap: wrap;
      }
    }

    &_l {
      @include base.tab {
        width: 65%;
        height: 100%;
      }

      & > .flex {
        @include base.tab {
          height: 100%;
        }
      }
    }

    &_r {
      @include base.tab {
        width: 30%;
        height: 100%;
      }
    }
  }
}

