        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        top: 58%;
        left: 2.5%;
        transform: translateY(-50%);

        @include base.tab {
          width: 50px;
          height: 50px;
          left: 5%;
        }
      }

      &::before,
      &::after {
        content: "";
        display: inline-block;
        position: absolute;
        width: 50px;
        height: 1px;
        background-color: #333;
        top: 50%;
        right: 5%;
        transition: all 0.4s;
      }

      &::before {
        transform: rotate(0deg);
      }

      &::after {
        transform: rotate(90deg);
      }

      &.active {
        &::before {
          transform: rotate(45deg);
        }

        &::after {
          transform: rotate(135deg);
        }
      }
    }

    &-answer {
      display: none;
      margin: 0 auto;
      margin-top: 2rem;
      padding-top: 2rem;
      width: 90%;
      border-top: 1px solid #ccc;
      @include l_height(16, 26);

      @include base.tab {
        @include l_height(18, 28);
      }

      &.active {
        display: inherit;
      }
    }
  }
}

